import React from 'react';

import TopSection from '../components/TopSection';
import Section from '../components/Section';

export default ({ classes, data }) => {
    return (
        <div>
            <TopSection
                sizesBackground={data.imageBackground.childImageSharp.sizes}>
                <h1>{data.contents.childStringsToml.faq.title}</h1>
                <p>{data.contents.childStringsToml.faq.description}</p>
            </TopSection>
        </div>
    );
};

export const query = graphql`
    query IndexFaqQuery {
        ...IndexLayoutFragment
        ...IndexFaqFragment
    }

    fragment IndexFaqFragment on RootQueryType {
        imageBackground: file(relativePath: { eq: "bg_faq.jpg" }) {
            childImageSharp {
                sizes(maxWidth: 984) {
                    ...GatsbyImageSharpSizes
                }
            }
        }

        contents: file(relativePath: { eq: "strings.toml" }) {
            childStringsToml {
                faq {
                    title
                    description
                }
            }
        }
    }
`;



